<!--  -->
<template>
  <div class>
    <el-row>
      <el-col :span="24" class="top">
        <div class="title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="wrap">
          <div class="count">: 0</div>
          <div class="time">
            <span style="margin-right:15px;font-size:14px;color:#ccc;margin-left:5px"></span>
            <el-date-picker
              v-model="params.value1"
              type="daterange"
              value-format="yyyy-MM-dd"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
            <el-button style="width:100px" @click="search"></el-button>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="createTime" label="" :formatter="dateFormat" align="center"></el-table-column>
      <el-table-column prop="remark" label="" :formatter="formatRole"  align="center"></el-table-column>
      <el-table-column prop="adminBonus" label="" align="center">
        <template slot-scope="scope"> 
            {{scope.row.adminBonus/100}}
         </template>
      </el-table-column>
    </el-table>
    <el-pagination  
      class="pagination"
      background
      @current-change="handleCurrentChange"
      align="center"
      :page-size="this.params.size"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
const token = sessionStorage.getItem("token");

export default {
  //import
  components: {},
  data() {
    return {
      total:0,
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      params:{
        value1: "",
        page:0,
        size:10
      },
      tableData: [
        
      ]
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    dateFormat(row, column) {
      var date = row[column.property];
      if (date === undefined) {
        return "";
      }
      return this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
     handleCurrentChange(val){
       this.params.page = val-1
       this.showPage()
     },
    showPage(){
      var params = this.params

      for (var key in params) {
          
          if (params[key] === "") {
           
            delete params[key];
          }
        }

      let {value1,page,size} = this.params
      if(value1){
        var begin = value1[0]
        var end = value1[1]
      }

      if(value1 || page || size){
        this.$axios.get('/income',{
        params:{
          begin,
          end,
          page,
          size
        },
        headers: {
                "Content-type":
                  "application/x-www-form-urlencoded; charset=utf-8"
              }
      }).then(res => {
        this.tableData = res.data.data.list
        this.total = res.data.data.totalCount
      })
      }
      
    },
    search(){

      if (sessionStorage.getItem("token")) {
        console.log("111");
        // this.formInline.page - 1
        let { value1 } = this.params;
        if (value1) {
          this.showPage()
        } else {
          this.$message({
            message: "",
            type: "warning"
          });
        }  
      }else{
        this.$message({
          message: ",",
          type: "warning"
        });

        // 
        this.$router.push({ path: "/Login" });
      }
    },
    cellStyle() {
      return "text-align:center";
    },
    rowClass() {
      return "text-align:center";
    },
    formatRole(row, column) {
      let name = row[column.property];
      let n = "" + name;
      return n;
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.showPage()
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='scss' scoped>
.top {
  width: 100%;
  height: 150px;
  background: pink;

  .title {
    background: #eee;
    border: 1px solid #ccc;
    border-bottom: 0px;
  }

  .wrap {
    background: #fff;
    border: 1px solid #ccc;
    height: 115px;

    .count, .time {
      margin-top: 15px;
      padding-left: 20px;
    }
  }
}

.el-table {
  border: 1px solid #ccc;
  margin-top: 15px;
}
.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;

}
.pagination {
  margin-top:20px;
  text-align:center;
}
</style>


