const { date }= require('start-date');
export default {
    data() {
        return {
			endDay: '',
			startDay: ''
        }
    },
    methods: {

        //
        buttomTwoChart(data) {
            // 
            let myChart = this.$echarts.init(
                document.getElementById("buttomTwoChart")
            );

            // 
            myChart.setOption({
                title: {
                    text: "",
                    subtext: "    "
                },
                tooltip: {
                    trigger: "axis"
                },
                legend: {
                    data: [""]
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    data: data.keyArr
                },
                yAxis: [
                    {
                        type: "value",
                        axisLabel: {
                            formatter: '{value} '
                        }
                    }
                ],
                series: [
                    {
                        name: "",
                        type: "line",
                        stack: "",
                        data: data.valArr
                    }
                ]
            });
        },
        //
        buttomChart(data) {
            // 
            let myChart = this.$echarts.init(document.getElementById("buttomChart"));

            // 
            myChart.setOption({
                title: {
                    text: "",
                    subtext: "    "
                },
                tooltip: {
                    trigger: "axis"
                },
                legend: {
                    data: [""]
                },
                grid: {
                    left: "3%",
                    right: "4%",
                    bottom: "3%",
                    containLabel: true
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    data: data.keyArr
                },
                yAxis: [
                    {
                        type: "value",
                        // interval: , //
                        // minInterval: 100, //1
                        axisLabel: {
                            formatter: '{value} '
                        }
                    }
                ],
                series: [
                    {
                        name: "",
                        type: "line",
                        stack: "",
                        data: data.valArr
                    }
                ]
            });
        },
        // 
        rightChant() {
            //  
            let myChart = this.$echarts.init(document.getElementById("chart"));
            // 
            myChart.setOption({
                xAxis: {
                    type: "category",
                    data: [
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                        ""
                    ]
                },
                yAxis: [
                    {
                        type: "value",

                        min: 0,
                        max: 1,
                        interval: 0.2, //
                        minInterval: 1, //1
                        axisLabel: {
                            // formatter: '{value} '
                        }
                    }
                ],
                series: [
                    {
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: "line"
                    }
                ]
            });
        },
        getDayStatistics(begin, end) {
            this.$axios.get(`/webSite/dayStatistics?begin=${begin}&end=${end}`).then((res) => {
                console.log(res.data)
                const data = res.data.data
                const kvData = this.obj2Arr(data.newUserMap)
                const kvData2 = this.obj2Arr(data.packMap)
                this.buttomChart(kvData)
                this.buttomTwoChart(kvData2)
                this.rightChant()
            })
        },
        obj2Arr(obj) {
            let keyArr = []
            let valArr = []
            keyArr = Object.keys(obj).sort()
            keyArr.forEach((item) => {
                valArr.push(obj[item])
            })
            console.log(keyArr, valArr)
            return { keyArr, valArr }
        },
        getDate(num){
            return date(num).split('-').reverse().join('-')
        }
    }
}


