<template>
  <section>
    <el-form class="toolbar" label-width="100px" style="margin-top:10px;padding-top:20px;">
      <el-row>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="account"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="qq">
            <el-input v-model="qq"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="wx"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="inviteAccount"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-select style="width:100%" v-model="farPlace" placeholder="">
              <el-option label="1%" value="1"></el-option>
              <el-option label="2%" value="2"></el-option>
              <el-option label="2%" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" @click="shousuo"></el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-table
      highlight-current-row
      :data="dataList"      
      style="width: 100%;"
    >
        <el-table-column align="center" prop="webSiteName" label=""></el-table-column>
        <el-table-column align="center" prop="account" label=""></el-table-column>
        <el-table-column align="center" prop="farPlace" label=""></el-table-column>
        <el-table-column align="center" prop="inviteCode" label=""></el-table-column>
        <el-table-column align="center" prop="qq" label="qq"></el-table-column>
        <el-table-column align="center" prop="wx" label=""></el-table-column>
        <el-table-column align="center" prop="vip2" label=""></el-table-column>
        <el-table-column width="200px" align="center" label="">
            <template slot-scope="scope">
                <el-button type="small" @click='editClick(scope.row)'></el-button>
            </template>
        </el-table-column>
    </el-table>
    <div style="padding:20px;text-align:center;">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <subscriber1Dia 
        v-if='editState' :data="rowData" 
        @cancel="editCancle"
        @confirm="editConfrim"
    />
  </section>
</template>

<script>
import subscriber1Dia from './subscriber1Dia';
export default {
  data() {
    return {
        total: 0,
        page: 1,
        size: 10,
        dataList: [],
        account: "",
        qq: "",
        wx: "",
        inviteAccount: "",
        farPlace: "",
        dialogVisible: false,
        value: "",
        currentIndex: 0, //
        row: {},
        isShowForm: false,
        editState: false,
        rowData: null, //row
    };
  },
  components:{
    subscriber1Dia
  },
  methods: {
    editClick(row){
        this.rowData = row;
        this.editState = !this.editState;
    },
    editCancle(){
        this.editState = false
    },
    editConfrim(){
        this.shousuo();
        this.editCancle();
    },
    shousuo: function() {
      let that = this;
      this.axios({
        method: "GET",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        url:
          "/user?page=" +
          (that.page - 1) +
          "&size=" +
          that.size +
          "&account=" +
          that.account +
          "&qq=" +
          that.qq +
          "&wx=" +
          that.wx +
          "&inviteAccount=" +
          that.inviteAccount +
          "&farPlace=" +
          that.farPlace
      }).then(function(res) {
        if (res.data.code == 200) {
          that.dataList = res.data.data.list;

          that.total = res.data.data.totalCount;

          that.dataList.forEach(item => {
            if (item.vip == 0) {
              item.vip2 = "";
            } else if (item.vip == 1) {
              item.vip2 = "vip";
            } else if (item.vip == 2) {
              item.vip2 = "vip";
            }
          });
        }
      });
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.size = val;
      this.shousuo();
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.page = val;
      this.shousuo();
    }
  },
  created() {
    this.shousuo();
  },
};
</script>



